    min-width: 200px;
    height: 20px;
    width: 60%; }
    .hangman .bar::after {
      content: "";
      background-color: #53555d;
      position: absolute;
      top: 20px;
      left: 170px;
      height: 40px;
      width: 18px; }
  .hangman .head {
    background-image: url(./head.png);
    position: absolute;
    top: 60px;
    left: 144px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 70px;
    width: 70px; }
  .hangman .neck {
    background-color: #fece8a;
